export const getApiResource = async (url) => {
  try {
    const res = await fetch(url);
    if (!res.ok) {
      console.error(res.status);
      return false;
    }
    return await res.json();
  } catch (error) {
    console.error(error.message);
    return false;
  }
};
/**
 *
 * @param url -   
 * @returns {Promise<Awaited<response>[array]>} -      url,   
 *   Url   (await Promise.all),      map    
 *   url  .
 *  url   (res)   fetch(res)  then    url   json()
 *    url    film
 * conCurrentFilms -     film
 */
export const conCurrentFilms = async (url) => {
  const film = await Promise.all(
    url.map((res) => fetch(res).then((res) => res.json()))
  );
  return film;
};
